export class GlobalEventDistributor {
  constructor() {
    // ,,api
    this.stores = [];
  }

  // 
  registerStore(store) {
    this.stores.push(store);
  }

  // ,. api
  // ,.,.
  dispatch(event) {
    this.stores.forEach(s => {
      s.dispatch(event);
    });
  }

  // 
  getState() {
    let state = {};
    this.stores.forEach(s => {
      let currentState = s.getState();
      state[currentState.namespace] = currentState;
    });
    return state;
  }
}
